<script >
	import { createEventDispatcher } from 'svelte';
    const dispatch = createEventDispatcher();

    export let url
    let name

    // const elements = new Set();
    export let summaries = []; // {url:'youtube/DyTCOwB0DVw', updated: '2012-01-01T06:25:24Z'}

    function selectVideo(s){
        console.log(s);
        url = s.url
    }

    export function add(summary = {}){
        let {url} = summary
        if (!url) return

        let i
        if((i = summaries.findIndex( s => s.url === url )) !== -1) {
            summaries[i] = summary
        } else {
            summaries = [...summaries, summary]
        }
        summary.updated = new Date() 
    }

    function sort(){
        summaries.sort((a, b) => new Date(a.updated).getTime() - new Date(b.updated).getTime() );
    }


    function cancelSummary(){
        url = undefined
    }

    export function saveSummary () {
        add({url, name})
        dispatch('save', summaries);
    }

</script>


<div class="inline-flex m-2 p-1 rounded-md bg-gray-700">
    <div hidden={!!url} >
        <input class="bg-white border border-gray-200 rounded-md px-2 h-5" type="text" placeholder="youtube video link" bind:value={url}  > 
    </div>
    <div class="" hidden={!url}>
        <!-- <input class="bg-white border border-gray-200 rounded-md px-2 h-5" type="text" placeholder="name" bind:value={name}  >  -->
        <button class="opacity-100 bg-blue-500 rounded-md text-white" hidden={!url} on:click={saveSummary} >Save</button>
        <button class="opacity-100 bg-red-500 rounded-md text-white" hidden={!url} on:click={cancelSummary} >Cancel</button>
    </div>
</div>
<div hidden={!!url}>
    <div class="text-xs">
    {#each summaries as summary}
        <p class="hover:bg-blue-400" on:click={() => selectVideo(summary)}>
            <span class="hover:text-white" >{summary.url}</span>
            <span class="m-1 italic text-gray-300">{summary.updated}</span>
        </p>
    {/each}
    </div>
</div>


    