// let fragmentation = []
let fragmentation = []
// {start: 10, end: 20}, {start: 50, end: 100}, {start: 162, end: 232}

function setFragmentation(frag){
    fragmentation = frag
}
function getFragmentation(){
    return fragmentation
}


const addFragment = ({currentTime=0, duration=0}={}) => {
    if(currentTime < 0 || currentTime >= duration ) return fragmentation

    let fragment = {start:currentTime, end: duration} 
    
    if(!searchFragment(fragment)) {
        fragmentation.push(fragment)
        fragmentation.sort((a, b) => (a.start - b.start))
        rewriteEnd(fragment);    
    }
    
    return fragmentation
}
const searchFragment = (fragment={}) => {
    // check duplicate
    return fragmentation.some(el => el.start === fragment.start);
}

const rewriteEnd = (fragment) => {
    let index = fragmentation.indexOf(fragment);
    if (index > 0) {
        if (fragmentation[index - 1].end > fragment.start)
        fragmentation[index - 1].end = fragment.start;
    }
    if(index +1  < fragmentation.length) {
        if (fragment.end > fragmentation[index + 1].start )
        fragment.end = fragmentation[index + 1].start;        
    }
}




const calculatePercentage = ( duration = 0 ) => {
    let acc = 0
    let percentages = []

    if(duration !== 0) {
        for (const fragment of fragmentation) {
            let {start=0, end=0} = fragment
            let empty = 100*start/duration  - acc
            acc += empty
            let percentage =  100*end/duration - acc 
            acc += percentage
            percentages.push({ empty, percentage, fragment } ) 
        }
    }
    
    return percentages
}

function clear(){
    fragmentation = []
}

export default {getFragmentation, setFragmentation,
    rewriteEnd,
    searchFragment,
    addFragment, 
    calculatePercentage,
    clear
}

