<script>
	import { Player, Boot, FileProvider, YouTubeProvider } from "@vime-js/complete" // , Settings, DefaultSettings
	import Summaries from './Summaries.svelte';

	import FragManager from './fragment'

	let summaries = JSON.parse(localStorage.getItem('summaries')) || []

	function saveSummary({detail: summaries}){
		console.log('saveSummary',summaries);
		
		localStorage.setItem(summaryUrl, JSON.stringify(FragManager.getFragmentation()) )		
		localStorage.setItem('summaries', JSON.stringify(summaries) )
	}

	let player = {duration:0}
	let playerPaused = true


	let summaryUrl
	$: if (summaryUrl) {
		let store =  JSON.parse(localStorage.getItem(summaryUrl) || "[]");
		FragManager.setFragmentation(store)
	}





	let fragmentationPercentage = [] 
	let mark = false
	let markStart = 0



  	const onPlayerMount = () => {
		// All props below are available here.
		const { paused, isVideoReady } = player.getStore();

		const unIsVideoReady = isVideoReady.subscribe(videoReady => {
			fragmentationPercentage = calculatePercentage() 

		});

		const unsubscribe = paused.subscribe(isPaused => {
			playerPaused = isPaused
		});

	}


	const markBucle = () => {
		if (seeking) return
		mark ? addFragment() : markStart = player.currentTime
		mark = !mark
	}

	const addFragment = (e) => {
		FragManager.addFragment({currentTime: markStart, duration: player.currentTime} )
		fragmentationPercentage = calculatePercentage() 
	}


	const calculatePercentage = () => {
		return FragManager.calculatePercentage(player && player.duration || 0)
	}

	let seeking = false
	let myInterval
	const seek = (event,percentage ) => {
		event.stopPropagation()


		if(!seeking){
			event.target.style.backgroundColor = 'green'
			player.currentTime = percentage.fragment.start
			myInterval = setInterval(() => {
				if(player.currentTime > percentage.fragment.end ) {
					player.currentTime = percentage.fragment.start
				}
			}, 1000);			
		} else {
			event.target.style.backgroundColor = ''
			myInterval && clearInterval(myInterval)
		}
		seeking = !seeking		
	}


	function clear(){
		FragManager.clear()
		fragmentationPercentage = []
	}

</script>

<style global>
  	@tailwind base;
  	@tailwind components;

  	.tooltip .tooltip-text {
		visibility: hidden;
		position: absolute;
		z-index: 100;
	}
	.tooltip:hover .tooltip-text {
		visibility: visible;
	}
 
  @tailwind utilities;
</style>

<main>
	<div class="fixed {playerPaused?'z-10':''}">
 			<Summaries {summaries} bind:url={summaryUrl} on:save={saveSummary}></Summaries>
	</div>

	{#if !!summaryUrl} 
	<div >
		<Player
		src={summaryUrl}
		plugins={[Boot]}
		providers={[FileProvider, YouTubeProvider]}
		on:mount={onPlayerMount}
		bind:this={player} />
		<div class="bar-container mr-4 h-3 w-full bg-gray-300 flex {mark? 'bg-red-500': ''}" on:click={markBucle}  >
		{#each fragmentationPercentage as fp}
			<div class="bg-white tooltip" style="width: {fp.empty}%;">
				<span class="tooltip-text bg-green-200 rounded mt-3">{Math.round(fp.fragment.start)}</span>
			</div>
			<div class="bg-red-900 tooltip px-px" style="width: {fp.percentage}%;" on:click={(e) => seek(e,fp)} >
				<span class="tooltip-text bg-red-300 rounded mt-3">{Math.round(fp.fragment.end)}</span>
			</div> 
		{/each}
		</div>
	</div>
	{/if}


</main>
